<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->
    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey">
              Bu sayfa temsili bir sayfadır. Gerçek randevu sisteminde
              bulunmayacaktır.
            </h3>
            <h4 style="color: grey">
              Test yapabilmek için aynı anda hem bilgisayarda hem telefonda
              açılmalıdır.
            </h4>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title class="mb-0"
              >QR kod ile temassız sıra alma hakkında</v-card-title
            >
            <v-card-text>
              <h3>Bu sistemde iki farklı olasılık bulunmaktadır.</h3>
              <ol>
                <li>
                  <h4>Olasılık</h4>
                  <p>
                    Vatandaş randevu aldığında kendisine SMS ile kişiye özel bir
                    bağlantı gönderilir. Bu bağlantıya tıkladığında QR kod
                    okuyabileceği bir ekran açılır. QR koddan vatandaşın
                    müdürlüğe gittiği tespit edilip sıra alması sağlanır.
                  </p>
                </li>
                <li>
                  <h4>Olasılık</h4>
                  <p>
                    Vatandaş müdürlüğüe gittiğinde akıllı telefonundaki QR kod
                    okuyucu ile müdürlükteki hazırlanmış olan kodu okutur. QR
                    koddan vatandaşın hangi müdürlükte olduğu tespit edilir ama
                    kimliği tespit edilemez. Bunun için kimlik numarası ve
                    birkeç ek bilgi istenerek kimlik tespiti yapılır. Eğer
                    randevusu varsa sıra alması sağlanır. Randevusu yoksa sıra
                    alması için randevusuz işlemler menüsüne yönlendirilir.
                  </p>
                </li>
              </ol>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title class="mb-0">Temsili SMS bağlantısı</v-card-title>
            <v-card-text>
              <h3>Bu bağlantıya akıllı telefondan tıklanmalıdır.</h3>
              <h3>
                Bağlantı açıldıktan sonra kamera erişimine izin verilmelidir.
              </h3>

              <div>
                <br />
                <ul>
                  <li v-for="(kisi, i) in getKisiList" :key="i">
                    <h2>TC:{{ kisi.tc }} Adı: {{ kisi.adi }}</h2>
                    <h4 v-if="kisi.randevu">(RANDEVULU)</h4>
                    <h4 v-if="!kisi.randevu">(RANDEVUSUZ)</h4>
                    <a v-if="kisi.randevu" :href="kisi.url.href">{{
                      kisi.url.text
                    }}</a>
                    <br />
                    {{ kisi.data }}
                    <br />
                    <br />
                  </li>
                </ul>
              </div>
              <br />
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col>
          <v-card class="mx-auto mb-0">
            <v-card-title class="mb-0">QR kod bağlantısı</v-card-title>
            <v-card-text>
              <h3>Bu bağlantı QR okuyucudan elde edilen bağlantıdır.</h3>

              <br />
              <div>
                <br />
                <ul>
                  <li v-for="(mudurluk, i) in getIlceList" :key="i">
                    <h2>{{ mudurluk.mudurluk.adi }}</h2>
                    <h3>{{ mudurluk.mudurluk.t }}</h3>
                    <a :href="mudurluk.url.href">{{ mudurluk.url.text }}</a>
                    <br />
                    {{ mudurluk.data }}
                    <br />
                    <v-btn
                      color="success"
                      elevation="4"
                      class="mr-5"
                      @click="createQR(mudurluk.url.text)"
                    >
                      QR KOD
                    </v-btn>
                    <br />
                    <br />
                  </li>
                </ul>
              </div>

              <!-- <a :href="getHref(mudurluk).href">{{ getHref(mudurluk).text }}</a> -->
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="dialog" width="350">
      <v-card class="mx-auto">
        <v-card-title class="mb-0">QR kodu okutabilirsiniz</v-card-title>
        <v-card-text>
          <canvas id="canvas" height="360"></canvas>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import QRCode from "qrcode";
export default {
  data: () => ({
    dialog: false,
    mudurluk: {
      mid: 789,
    },
  }),
  methods: {
    getHref(base, data) {
      var link = {
        href: base + data,
        text:
          window.location.origin + process.env.VUE_APP_BASEURL + base + data,
      };
      return link;
    },
    createQR(link) {
      this.dialog = true;

      setTimeout(() => {
        QRCode.toCanvas(
          document.getElementById("canvas"),
          link,
          { width: 300 },
          (error) => {
            if (error) console.error(error);
          }
        );
      }, 100);
    },
  },
  computed: {
    getKisiList() {
      return this.$store.state.kisiListesi.map((k) => {
        var data = {
          adi: k.adi,
          tc: k.tc,
          randevu: k.randevu,
          url: this.getHref(
            "#/qr/",
            btoa(JSON.stringify({ tc: k.tc, typ: k.typ }))
          ),
          data: { tc: k.tc, typ: k.typ },
        };
        return data;
      });
    },
    getIlceList() {
      return this.$store.state.ilceListesi.map((k) => {
        var data = {
          mudurluk: k,
          url: this.getHref(
            "#/qr/",
            btoa(JSON.stringify({ id: k.id, h: k.h, t: k.t, typ: k.typ }))
            //k.typ+"|"+k.id+"|"+k.t.toJSON()+"|"+k.h
          ),
          data: { id: k.id, h: k.h, t: k.t, typ: k.typ },
        };
        return data;
      });
    },
  },
  mounted() {
    var kisiler = [
      {
        typ: "p",
        tc: 123,
        adi: "Hasan Kıvılcım",
        randevu: true,
        mudurlukId: 12,
      },
      { typ: "p", tc: 456, adi: "Mehmet Aybaş", randevu: true, mudurlukId: 12 },
      { typ: "p", tc: 789, adi: "Ayşe Türk", randevu: true, mudurlukId: 10 },
      { typ: "p", tc: 321, adi: "Mahmut Kıvılcım", randevu: false },
      { typ: "p", tc: 654, adi: "Emine Sur", randevu: false },
      { typ: "p", tc: 987, adi: "Ayfer Afakan", randevu: false },
    ];
    this.$store.commit("setKisiListesi", { data: kisiler });
    var ilceler = [
      {
        typ: "m",
        id: 10,
        adi: "Keçiborlu",
        gecerlimi: true,
        t: new Date(),
        h: "1234567890yuiopasqwertdfghjklzxc",
      },
      {
        typ: "m",
        id: 11,
        adi: "Muratpaşa",
        gecerlimi: true,
        t: new Date(),
        h: "123456uiertyhjklzqwxcopasdfg7890",
      },
      {
        typ: "m",
        id: 12,
        adi: "Çankaya",
        gecerlimi: true,
        t: new Date(),
        h: "123459tyuio0qwerjklzxcp678asdfgh",
      },
      {
        typ: "m",
        id: 13,
        adi: "Yenimahalle",
        gecerlimi: false,
        t: new Date("2021-05-20T03:24:00"),
        h: "123tyuiop45678xc9erasdf0qwghjklz",
      },
    ];
    this.$store.commit("setIlceListesi", { data: ilceler });
  },
};
</script>